<template id="listBody">
  <div class="list">
    <img class="carImg" alt="carImg" :src="image">
    <h1>Randomize a car for you to get</h1>
    <dl v-if="cars">
      <template v-for="car in cars">
        <ul :key="car.Mfr_ID">
          <li>{{ car.Mfr_Name }}</li>
        </ul>
      </template>
    </dl>
    <p v-else>Getting Car...</p>
    <b-button id="button" v-on:click="getCar" variant="primary">
      Click here to get a car
    </b-button>
  </div>
</template>

<script>
import axios from "axios";
import image from "../assets/bmwcar.png"

export default {
  //v-bind
  name: "fetch",
  created() {
    axios
      .get(
        `https://vpic.nhtsa.dot.gov/api/vehicles/getallmanufacturers?format=json`
      )
      .then((response) => {
        this.cars = response.data.Results;
      })
      .catch((e) => {
        this.errors.push(e);
      });
  },
  data() {
    return {
      cars: null,
      styleObject: {
        color: "grey",
        fontSize: "20px",
      },
      image: image
    };
  },
  props: {},
  methods: {
    getCar() {
      let num = Math.ceil(Math.random() * 20);
      alert(this.cars[num].Mfr_CommonName);
    },
  },
};
</script>

<style scoped>
.carImg {
  height: 100px;
  width: 150px;
}
#list {
  background-color: rgb(79, 101, 121);
}
#button {
  color: whitesmoke;
}
</style>